.blog__page {
  flex-direction: column;
  align-items: center;
  margin-top: 30px;
  display: flex;
}

.search__container {
  min-width: 288px;
}

.blog__page-container {
  max-width: 288px;
  margin: auto;
}

.search__form {
  display: none;
}

.blog__page-galery-container-ul {
  flex-direction: column;
  gap: 20px;
  display: flex;
}

.blog__page-galery-container-ul-li-img {
  height: 328px;
}

.blog__page-container-title {
  color: #fff;
  font-family: Gilroy;
  font-size: 30px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}

.blog__page-galery-container {
  margin-top: 30px;
}

.li__bottom-item {
  width: 100%;
  height: 248px;
  border: 1px solid #fcd54c;
  flex-direction: column;
  justify-content: center;
  padding: 20px;
  transition: background-color 1s;
  animation-play-state: running;
  display: flex;
}

.li__bottom-item:hover, .li__bottom-item:focus {
  background-color: #fcd54c;
}

.li__bottom-item:hover > .li__bottom-item-title, .li__bottom-item:focus > .li__bottom-item-title, .li__bottom-item:hover > .li__bottom-item-text, .li__bottom-item:focus > .li__bottom-item-text, .li__bottom-item:hover > .li__bottom-item-info, .li__bottom-item:focus > .li__bottom-item-info {
  color: #272727;
  stroke: #272727;
}

.li__bottom-item-title {
  color: #fff;
  font-family: Gilroy;
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  line-height: 35px;
}

.li__bottom-item-text {
  color: #929292;
  padding-top: 22px;
  font-family: Gilroy;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 21px;
}

.li__bottom-item-info {
  color: #fff;
  stroke: #fff;
  align-items: center;
  padding-top: 28px;
  display: flex;
}

.li__bottom-item-info-another-text {
  color: inherit;
  font-family: Work Sans;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 24px;
}

.li__bottom-item-arrow {
  width: 11.322px;
  height: 20.025px;
  margin-left: 20px;
}

@media screen and (min-width: 768px) {
  .blog__page-container {
    max-width: 694px;
  }

  .search__container {
    min-width: 694px;
    justify-content: space-between;
    display: flex;
  }

  .search__form {
    min-width: 510px;
    height: 100%;
    border: 1px solid #929292;
    justify-content: space-between;
    margin-left: 76px;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 20px;
    display: flex;
  }

  .search__form-input {
    background-color: #272727;
  }

  .blog__page-container-title {
    font-size: 50px;
  }

  .blog__page-galery-container-ul {
    flex-flow: wrap;
    gap: 29px;
  }

  .blog__page-galery-container-ul-li {
    width: 332px;
  }

  .li__bottom-item-text {
    font-size: 16px;
  }
}

@media screen and (min-width: 1920px) {
  .search__container {
    min-width: 100%;
    padding-left: 62px;
    padding-right: 62px;
  }

  .blog__page-container-title {
    font-size: 60px;
  }

  .blog__page-container {
    min-width: 1200px;
  }

  .blog__page-galery-container {
    margin-top: 70px;
  }
}



